import axios from 'axios';
import React from 'react';
import roomHead from '../img/head2.jpg';
import { Popover,Button,message} from 'antd';
import {getuserlist} from '../redux/usermsg.redux';
import {connect} from 'react-redux';

@connect(
    state=>state,
    {getuserlist}
)
class User extends React.Component{
    constructor(props){
        super(props)
        this.state={
            color:"rgba(160, 241, 247, 0.5)"
        }

    }

    componentDidMount(){
        this.setState({
            username:this.props.username
        })
    }
    
    handleClickSendUser(){
        /* TODO:
        1.
        2.
        3.roomlist
        4.namespacesocketnamespacerecivemsg
        socketnamespace+
        5.
        */
        let myname = this.props.user.username;
        let friend = this.state.username;
        let data = {
            "myname":myname,
            "friend":friend
        }
        if(myname === friend){
            message.warning("!!!")
        }else{
            axios.post("/chat/createmyusers/",data).then(
                res=>{
                    if(res.status === 200 && res.data.code === 0){
                        this.props.getuserlist(this.props.user.username)
                    }else if(res.status === 200 && res.data.code === 1){
                        message.warning(res.data.error)
                    }
                }
            )
        }
        
    }

    render(){
        const content = (
            <div>
              {/* <div className="onlineBotton">
                  <Button ghost ></Button>
              </div> */}
              <div className="onlineBotton">
                  <Button ghost onClick={this.handleClickSendUser.bind(this)}></Button>
              </div>
            </div>
          );
        return(
            <Popover placement="bottom" overlayClassName="onlineUserPop" content={content} trigger="click" className="useronlinepop">
                <div className="user" style={{backgroundColor:this.state.color}}>
                    
                    <img src={this.props.headimg} alt="" className="round_icon"/>
                    
                    <div className="userlist">
                        <span>{this.props.username}</span>
                    </div>

                </div>
                <div style={{width:223,height:2,margin:0,padding:0,backgroundColor:"#D5D5D5",overflow:"hidden"}}></div>
            </Popover>
        )
    }
}
export default User